// Service d'authentification pour l'application IKIGAI
// Gère les connexions, inscriptions et déconnexions d'utilisateurs

import { supabase, handleSupabaseError } from '../../shared/supabase';

class AuthService {
  // Récupérer l'utilisateur actuel
  static async getCurrentUser() {
    try {
      const { data: { user }, error } = await supabase.auth.getUser();
      if (error) throw error;
      return user;
    } catch (error) {
      console.error('Erreur lors de la récupération de l\'utilisateur:', error);
      return null;
    }
  }
  
  // Inscription avec email et mot de passe
  static async signUp(email, password) {
    try {
      const { data, error } = await supabase.auth.signUp({
        email,
        password
      });
      
      // Vérifier s'il y a une erreur
      if (error) {
        throw error;
      }
      
      // Vérifier si data.user existe
      if (!data || !data.user) {
        throw new Error("Erreur lors de l'inscription. Veuillez réessayer.");
      }
      
      return {
        user: data.user,
        success: true,
        message: 'Inscription réussie! Vérifiez votre email pour confirmer votre compte.'
      };
    } catch (error) {
      console.error('Erreur lors de l\'inscription:', error);
      
      // Utiliser la fonction utilitaire pour obtenir un message d'erreur approprié
      const errorMessage = handleSupabaseError(error);
      
      return {
        user: null,
        success: false,
        message: errorMessage || "Erreur lors de l'inscription. Veuillez réessayer."
      };
    }
  }
  
  // Connexion avec email et mot de passe
  static async signIn(email, password) {
    try {
      const { data, error } = await supabase.auth.signInWithPassword({
        email,
        password
      });
      
      // Vérifier s'il y a une erreur
      if (error) {
        throw error;
      }
      
      // Vérifier si data.user existe
      if (!data || !data.user) {
        throw new Error("Erreur lors de la connexion. Veuillez vérifier vos identifiants.");
      }
      
      return {
        user: data.user,
        success: true,
        message: 'Connexion réussie!'
      };
    } catch (error) {
      console.error('Erreur lors de la connexion:', error);
      
      // Utiliser la fonction utilitaire pour obtenir un message d'erreur approprié
      const errorMessage = handleSupabaseError(error);
      
      return {
        user: null,
        success: false,
        message: errorMessage || "Identifiants incorrects. Veuillez réessayer."
      };
    }
  }
  
  // Connexion avec Google OAuth
  static async signInWithGoogle() {
    try {
      const redirectUrl = window.location.origin;
      console.log('URL de redirection OAuth configurée:', redirectUrl);
      
      // Approche simplifiée de gestion des sessions OAuth
      const authSessionId = Date.now().toString();
      
      // Stocker l'information d'onboarding uniquement dans localStorage
      // pour éviter les problèmes de synchronisation entre différents mécanismes de stockage
      const existingOnboardingStatus = localStorage.getItem('onboardingCompleted');
      
      // Si nous n'avons pas d'information explicite que l'onboarding est complété,
      // nous supposons qu'il doit être effectué
      if (existingOnboardingStatus !== 'true') {
        console.log('Connexion Google - Statut onboarding non confirmé, configuration pour redirection');
        localStorage.setItem('onboardingCompleted', 'false');
        localStorage.setItem('authSessionId', authSessionId);
      } else {
        console.log('Connexion Google - Statut onboarding déjà confirmé comme complété');
      }
      
      // Options pour Supabase OAuth avec persistance de session
      const options = {
        redirectTo: redirectUrl,
        persistSession: true
      };
      
      const { error } = await supabase.auth.signInWithOAuth({
        provider: 'google',
        options: options
      });
      
      // Vérifier s'il y a une erreur
      if (error) {
        throw error;
      }
      
      return {
        success: true,
        message: 'Redirection vers Google...'
      };
    } catch (error) {
      console.error('Erreur lors de la connexion avec Google:', error);
      
      // Utiliser la fonction utilitaire pour obtenir un message d'erreur approprié
      const errorMessage = handleSupabaseError(error);
      
      return {
        success: false,
        message: errorMessage || "Erreur lors de la connexion avec Google. Veuillez réessayer."
      };
    }
  }
  
  // Déconnexion
  static async signOut() {
    try {
      const { error } = await supabase.auth.signOut();
      
      // Vérifier s'il y a une erreur
      if (error) {
        throw error;
      }
      
      return {
        success: true,
        message: 'Déconnexion réussie!'
      };
    } catch (error) {
      console.error('Erreur lors de la déconnexion:', error);
      
      // Utiliser la fonction utilitaire pour obtenir un message d'erreur approprié
      const errorMessage = handleSupabaseError(error);
      
      return {
        success: false,
        message: errorMessage
      };
    }
  }
  
  // Vérifier si un utilisateur est connecté
  static async isAuthenticated() {
    try {
      const user = await this.getCurrentUser();
      return !!user;
    } catch (error) {
      return false;
    }
  }
  
  // Réinitialisation de mot de passe
  static async resetPassword(email) {
    try {
      const { error } = await supabase.auth.resetPasswordForEmail(email, {
        redirectTo: `${window.location.origin}/reset-password`,
      });
      
      // Vérifier s'il y a une erreur
      if (error) {
        throw error;
      }
      
      return {
        success: true,
        message: 'Email de réinitialisation envoyé avec succès!'
      };
    } catch (error) {
      console.error('Erreur lors de la réinitialisation du mot de passe:', error);
      
      // Utiliser la fonction utilitaire pour obtenir un message d'erreur approprié
      const errorMessage = handleSupabaseError(error);
      
      return {
        success: false,
        message: errorMessage || "Erreur lors de l'envoi de l'email de réinitialisation. Veuillez réessayer."
      };
    }
  }
}

export default AuthService;